@function em($size, $context: 16) {
  @return ($size/$context) * 1em; }

@function img-url($image) {
  @return url('../img/#{$image}'); }

@function vw($px-vw, $base-vw: 1400px) {
  @return ($px-vw * 100vw) / $base-vw; }

@function vh($px-vh, $base-vh: 950px) {
  @return ($px-vh * 100vh) / $base-vh; }

@mixin image($image) {
  background-image: img-url($image); }

@mixin trans($what: all, $dur: 0.2s, $easing: ease) {
  transition: $what $dur $easing; }

@mixin smooth-scroll {
  -webkit-overflow-scrolling: touch; }

@mixin smooth-font {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

@mixin nfc {
  &:not(:first-child) {
    @content; } }

@mixin after {
  &:after {
    content: '';
    display: block;
    @content; } }

@mixin gpu {
  transform: translate3d(0,0,0); }

@mixin xxl {
  @include r(1680) {
    @content; } }

@mixin laptop {
  @include r(1440) {
    @content; } }

@mixin tablet {
  @include r(1200) {
    @content; } }

@mixin md {
  @include r(1023) {
    @content; } }

@mixin sm {
  @include r(767) {
    @content; } }

@mixin xs {
  @include r(599) {
    @content; } }

@mixin xxs {
  @include r(480) {
    @content; } }
