.s-video-rooms {
	background: #E2E2E2; }

.video-rooms {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	padding: 30px 0;
	gap: 40px;

	@include md {
		gap: 20px; }

	&__item {
		position: relative;
		padding: 32px 32px 32px;
		background: #fff;
		width: 297px;
		box-sizing: border-box;
		display: flex;
		flex-direction: column;
		align-items: center;
		transition: all ease 0.1s;

		@include xs {
			max-width: 100%; }

		&.hoverable {
			cursor: pointer;

			&:hover {

				&:hover {
					box-shadow: 0 3px 15px rgba(0, 0, 0, 0.2); }

				.video-rooms__btn {
					color: $themeColor; } } } }




	&__link {
		position: absolute;
		display: block;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0; }

	&__img-wrap {
		display: flex;
		width: 100%;
		height: 174px;
		justify-content: center; }

	&__img {
		display: block;
		margin:  auto;
		max-height: 100%; }

	&__num {
		display: none; }

	&__title {
		display: none; }

	&__users {
		margin: 38px auto 40px; }

	&__user {
		display: flex;
		align-items: center;
		flex: 1 1 auto;
		margin: 8px auto 0;

		&:first-child {
			margin: 0; } }

	&__avatar {
		width: 34px;
		height: 34px;
		margin: 0 10px 0 0;
		border-radius: 50%;
		object-fit: cover; }

	&__name {
		font-size: 14px;
		line-height: 1.28; }

	&__btn {
		display: flex;
		margin: auto 0 0;
		align-items: center;
		justify-content: center;
		height: 46px;
		padding: 0;
		font-size: 14px;
		@include bold;
		color: $black;
		text-transform: uppercase;
		text-decoration: none;
		border: 2px solid transparent;
		text-align: center;
		transition: all ease 0.1s; } }
