.s-event {
  padding-top: 80px;
  padding-bottom: clamp(80px, vw(160px), 160px);
  padding-bottom: 0;

  .container {} }

.event {

  &__techs-title {
    margin: clamp(36px, vw(76px, 1024px), 76px) 0 clamp(24px, vw(18px, 1024px), 32px);
    line-height: 1.8;
    font-size: clamp(18px, vw(18px, 1024px), 22px); }

  &__image {
    z-index: -1;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      filter: brightness(0.4); } }

  &__video {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 0;
    object-fit: cover;
    object-position: right center;
    filter: brightness(0.4);
    z-index: -1; }

  &__top {
    position: relative;
    margin: clamp(-80px, vw(-160px), -160px) clamp(42px, vw(84px), 84px) 0 clamp(-90px, vw(-180px), -180px);
    padding: clamp(80px, vw(160px), 160px) clamp(60px, vw(120px), 120px) 298px clamp(90px, vw(180px), 180px);

    @include laptop {
      margin: clamp(-80px, vw(-160px), -160px) clamp(-60px, vw(-120px), -120px) 0 clamp(-90px, vw(-180px), -180px);
      padding: clamp(80px, vw(160px), 160px) clamp(60px, vw(120px), 120px) vw(280px) clamp(90px, vw(120px), 120px); }

    @include md {
      padding-bottom: vw(120px, 1024px); } }

  &__descr {
    max-width: 884px; }

  &__bottom {
    display: flex;
    flex-wrap: wrap;
    margin: 0 clamp(42px, vw(84px), 84px) clamp(60px, vw(120px), 120px) clamp(-90px, vw(-180px), -180px);
    padding: 0 0 0 clamp(90px, vw(180px), 180px);

    @include laptop {
      flex-direction: column;
      padding-left: clamp(90px, vw(120px), 120px); }

    @include md {
      margin: 0;
      padding: vw(120px, 1024px) 0 clamp(40px, vw(120px), 120px); } }

  &__days {
    margin: -238px 0 0;
    flex: 0 0 auto;

    @include laptop {
      margin: vw(-238px) 0 0; }

    @include md {
      margin: 0; } }

  &__techs {
    flex: 1 1 auto;

    @include laptop {
      margin: 20px 0 0; } } }

.days {
  display: flex;
  flex-wrap: wrap;

  @include md {
    flex-direction: column;
    width: 100%; }

  &__item {
    position: relative;
    text-align: center;
    width: min-content;

    @include nfc {
      margin-left: clamp(50px, vw(100px), 100px);

      @include laptop {
        margin-left: clamp(50px, vw(100px), 100px); }

      .days__plus {
        display: block;

        @include md {
          display: none; } } }

    @include md {
      display: flex;
      flex-direction: column;
      width: auto;
      flex-wrap: wrap;
      width: 100%;
      padding: 0 0 0 240px;
      min-height: 192px;
      box-sizing: border-box;

      @include nfc {
        margin: 20px 0 0 0; } }

    @include xs {
      padding: 200px 0 0 0;

      @include nfc {
        margin: 50px 0 0 0; } } }

  @include laptop {
    width: max-content;
    flex-wrap: wrap; }

  @include xxs {
    width: 100%;
    justify-content: center;
    align-items: center;
    flex-direction: column; }

  &__plus {
    position: absolute;
    width: 69px;
    height: 69px;
    top: 206px;
    left: -50px;
    transform: translateX(-50%);
    display: none;

    @include laptop {
      top: clamp(104px, vw(206px), 206px);
      width: 69px;
      height: 69px;
      left: vw(-50px); }

    svg {
      fill: #fff;
      width: 100%;
      height: 100%;
      display: block; } }

  &__title {
    font-size: 38px;
    text-transform: uppercase;
    line-height: 1.22;

    @include laptop {
      font-size: clamp(26px, vw(38px), 38px); }

    @include md {
      order: 2;
      text-align: left;
      margin-top: 26px;

      br {
        display: none; } }

    @include xs {
      text-align: center;
      margin: 12px 0 12px; } }

  &__date {
    @include bold;
    position: relative;
    box-sizing: border-box;
    padding-bottom: 87%;
    width: 318px;
    margin: 20px 0 66px;
    background: url(../img/day-bg.svg) center center no-repeat;
    background-size: contain;

    @include laptop {
      width: clamp(220px, vw(318px), 318px);
      margin: clamp(10px, vw(20px), 20px) 0 clamp(32px, vw(66px), 66px); }

    @include md {
      position: absolute;
      left: 0;
      top: 0;
      width: 220px;
      height: 192px;
      padding: 0;
      margin: 0 20px 0 0;
      order: 1; }

    @include xs {
      left: 50%;
      transform: translateX(-50%); } }

  &__date-inner {
    font-size: 106px;
    line-height: 1;
    padding: 32px 0 0;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;

    @include laptop {
      padding: clamp(16px, vw(32px), 32px) 0 0;
      font-size: clamp(72px, vw(106px), 106px); }

    span {
      display: block;
      font-size: 32px;

      @include laptop {
        display: block;
        font-size: clamp(22px, vw(32px), 32px); } } }

  &__descr {
    text-align: left;
    font-size: clamp(16px, vw(18px), 18px);
    line-height: 2;
    order: 3;

    @include xs {
      text-align: center; } } }
