.techs {
    display: flex;
    flex-wrap: wrap;
    margin: 32px auto 0 0;
    padding: 0;
    align-items: stretch;
    box-sizing: border-box;

    @include xs {
        margin: 32px -10px 10px; }

    &__item {
        display: flex;
        justify-content: center;
        min-width: 100px;
        margin: 0 0 20px;
        flex-direction: column;
        padding: 0 5px;
        min-width: 100px;
        justify-content: flex-start;

        @include md {
            min-width: auto; }
        @include sm {
            width: 20%; }
        @include xs {
            width: 25%; }
        @include xxs {
            width: 33.3%; } }

    &__link {
        text-decoration: none;
        color: #fff;
        transition: color .1s;

        &:hover {
            text-decoration: underline;
            color: $themeColor; } }

    &__img-box {
        height: 80px;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 0 20px;

        @include sm {
            margin: 0 0 10px; }

        @include xs {
            margin: 0 0 0; }

        img {
            transform: scale(0.90);
            max-height: 80px;

            @include md {
                transform: scale(0.75); } } }

    &__title {
        font-size: 20px;
        text-align: center;
        margin: 0;

        @include md {
            font-size: 16px; }

        @include sm {
            font-size: 14px; }

        @include xs; } }
