.sec-checkout {
   padding: 60px 0;
   &__title {
      margin: 24px auto 42px;
      letter-spacing: 0.01em;
      text-align: center;
      font-size: clamp(30px, 7vw, 70px);
      font-weight: 900;
      line-height: 1.0;
      text-transform: uppercase;
      color: #00CA6E; }
   &__description {
      margin-bottom: clamp(35px, 10vw, 70px);
      line-height: 1.6;
      font-size: clamp(12px, 2vw, 16px);
      text-align: center; }
   @include md {
      .container {
         padding-left: 20px;
         padding-right: 20px; } }
   @include sm {
      padding: 40px 0;
      .sec-checkout__title {
         margin-bottom: 30px;
         margin-top: 0; } } }

.tito-wrapper {
   max-width: 900px;
   margin-left: auto;
   margin-right: auto;
   &__text {
      margin-top: 30px;
      font-size: 14px;
      text-align: center;
      max-width: 600px;
      margin-left: auto;
      margin-right: auto;
      a {
         color: #00CA6E; } }
   @include md {
      max-width: none; } }

.tito-widget {
   margin-top: 0;
   margin-bottom: 0;
   font-family: inherit;
   color: #fff;

   .tito-widget-form {
      max-width: none;
      background: transparent;
      border: none;
      padding: 0;
      .tito-release {
         border-bottom: 2px solid rgba(230,230,230, 0.31);
         margin-top: 20px;
         padding-bottom: 20px;
         &:first-child {
            margin-top: 0; }
         .tito-release--title {
            font-size: clamp(14px, 2vw, 18px);
            font-weight: 900;
            text-transform: uppercase;
            margin-bottom: 10px; }

         .tito-release-description {
            padding-top: 0;
            padding-right: 3vw;
            padding-bottom: 0;
            .tito-release-description--description p {
               color: rgba(#fff, 0.7);
               &:empty {
                  display: none; }
               @include sm {
                  font-size: 12px; } } }

         .tito-quantity {
            align-self: center;
            position: relative;
            background: transparent;
            border: 2px solid #00CA6E;
            border-radius: 100px;
            align-items: stretch;
            margin-left: 20px;

            .tito-quantity-input {
               border: none;
               background: transparent;
               height: 50px;
               color: #fff;
               &::placeholder {
                  color: #fff; }
               &:focus {
                  outline: 2px solid rgba(#fff, 0.6); } }

            .tito-release--decrement-quantity {
               & > a {
                  height: 44px;
                  background: transparent;
                  border-radius: 50%;
                  margin: 3px;
                  width: 44px;
                  color: #fff;
                  position: relative;
                  svg {
                     display: none; }
                  &:after, &:before {
                     content: '';
                     position: absolute;
                     top: 50%;
                     left: 50%;
                     margin-left: -1px;
                     height: 14px;
                     width: 2px;
                     background: #fff;
                     margin-top: -7px;
                     pointer-events: none; }

                  &:before {
                     transform: rotate(90deg); }

                  &:hover {
                     background: rgba(#000, 0.4); }

                  &:focus-visible {
                     outline: 2px solid rgba(#fff, 0.6); } }

               &:first-child > a {
                  &:after {
                     display: none; } } } }

         .tito-release--status {
            background: rgba(255, 255, 255, 0.15);
            border: none;
            height: 50px;
            min-width: 145px;
            align-self: center;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            margin-right: 0;
            color: #fff;
            margin-left: 20px;
            border-radius: 30px; }

         @include sm {
            flex-wrap: wrap;
            justify-content: space-between;
            margin-top: 15px;
            padding-bottom: 15px;
            .tito-release-description {
               min-width: 100%;
               padding-right: 0;
               padding-bottom: 15px; }

            .tito-quantity {
               .tito-quantity-input {
                  height: 44px; }

               .tito-release--decrement-quantity {
                  & > a {
                     height: 38px;
                     width: 38px; } } }

            .tito-release--status {
               min-width: 133px;
               height: 44px; } } }

      .tito-release--sold-out {
         .tito-release--title, .tito-price {
            color: #cccccc; }

         .tito-release-description--description,
         .tito-release-description--description p {
            color: #b4b4b4 !important; } }

      .tito-times {
         display: none; } }

   input[type=text] {
      height: 60px;
      border-radius: 100px;
      padding: 0 20px !important;
      font-size: 16px;
      border: none;
      font-weight: 700;
      font-family: 'Montserrat', sans-serif;
      box-shadow: none;
      &::placeholder {
         color: #000; }
      &:focus {
         outline: 2px solid rgba(#fff, 0.4); } }

   .tito-form-actions {
      align-items: center;
      padding-bottom: 0;
      .tito-discount {
         margin-bottom: 0; }
      .tito-submit, .tito-submit.tito-submit--discount-code {
         right: calc((100% - 900px) / 2);
         height: 60px;
         width: auto;
         border: 3px solid #00CA6E;
         background: #202A43;
         color: #fff;
         position: fixed;
         bottom: 70px;
         z-index: 11;
         padding: 0 50px;
         display: inline-flex;
         align-items: center;
         justify-content: center;
         text-align: center;
         min-width: 170px;
         font-size: 20px;
         font-weight: 700;
         font-family: 'Montserrat', sans-serif;
         border-radius: 100px;
         &:hover, &:focus-visible {
            background: #00CA6E; }

         @include md {
            right: 20px; }
         @include sm {
            right: 20px;
            padding: 15px 20px;
            font-size: 20px; } } } }

.tito-price-details-regular {
   font-size: clamp(22px, 3vw, 34px);
   font-weight: 900;

   .tito-release--tax {
      color: #838383 !important; }

   @include sm {
      .tito-release--tax {
         font-size: 9px !important; }
      .tito-price {
         text-align: left !important; } } }

.tito-overlay {
   a {
      color: inherit; } }

.tito-block._offset {
   .tito-submit, .tito-submit.tito-submit--discount-code {
      position: static; } }

@include md {
   .hidden-iphone {
      display: none !important; } }

@include sm {
   .tito-widget input[type=text] {
      height: 50px; }
   .tito-widget .tito-form-actions {
      min-height: 142px;
      align-items: flex-start;
      .tito-discount {
         margin-bottom: 20px; }
      .tito-submit,
      .tito-submit.tito-submit--discount-code {
         width: auto;
         left: 20px;
         height: 50px;
         font-size: 18px;
         display: flex; } }
   .tito-block._offset {
      .tito-submit,
      .tito-submit.tito-submit--discount-code {
         width: 100%;
         display: flex; } } }
