.s-location {
  padding: clamp(28px, vw(56px), 56px) 0;
  position: relative;
  max-width: 1920px;
  margin: 0 auto;
  width: 100%;

  @include sm {
    padding: 0; }

  .section {
    &__title {
      position: absolute;
      top: clamp(48px, vw(96px), 96px);
      width: 100%;
      max-width: 1458px;
      box-sizing: border-box;
      z-index: 3;
      left: 50%;
      transform: translateX(-50%);

      @include md {
        top: 50px; } } } }

.location {
  height: 626px;
  display: flex;
  flex-direction: column;
  width: 100%;
  box-sizing: border-box;
  @include md {
    height: auto; }

  &__inner {
    height: 100%;
    display: flex;
    flex-direction: column; }

  &__map {
    position: absolute;
    z-index: 1;
    left: 15%;
    top: 0;
    width: 100%;
    height: 100%;

    @include md {
      position: relative;
      height: 400px;
      left: auto; }
    @include sm {

      height: 300px; }


    #map {
      width: 100%;
      height: 100%; }

    .map {
      width: 100%;
      height: 100%;

      img {
        display: block;
        object-fit: none;
        width: 100%;
        height: 100%;
        object-position: calc(100% + 50px) center;

        @include laptop {
          object-position: calc(100% + 150px) center; }

        @include md {
          object-position: center center; }

        @include xs {
          object-position: center center; } } } }

  &__slider {
    position: absolute;
    z-index: 2;
    left: 0;
    top: -36px;
    bottom: -36px;
    width: 100%;

    @include md {
      position: relative;
      top: 0;
      bottom: -50px; } } }

.location-slider {
  .swiper-slide:not(.swiper-slide-active) {
    visibility: hidden; }
  @include md {
    .swiper-wrapper {
      align-items: flex-start;
      .swiper-slide:not(.swiper-slide-active) {
        height: 0;
        overflow: hidden; } } }

  &__item {
    display: flex;

    .container {
      position: relative;
      width: 100%; }

    img {
      display: block;
      position: absolute;
      height: 100%;
      width: 100%;
      object-fit: cover;
      filter: brightness(0.6);
      clip-path: polygon(0 0, calc(100% - 250px) 0, calc(100% - 716px) 100%, 0 100%);
      @include laptop {
        clip-path: polygon(0 0, calc(100% - 250px) 0, calc(100% - 516px) 100%, 0 100%); }
      @include md {
        clip-path: none; } } }

  &__content {
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 2;

    max-width: 1458px;
    box-sizing: border-box;
    padding-top: clamp(100px, vw(210px), 210px);
    padding-bottom: clamp(58px, vw(90px), 90px);
    padding-right: 470px;

    @include md {
      padding-right: 20px; } }

  &__title {
    text-transform: uppercase;
    font-size: clamp(28px, vw(78px), 78px);
    margin-left: -4px;
    color: $themeColor;
    font-weight: 700;
    line-height: 0.96;
    letter-spacing: 0.02em;
    margin-bottom: clamp(28px, vw(54px), 54px); }

  &__bottom {
    @include bold;
    max-width: 766px;
    font-size: clamp(16px, vw(22px), 22px);
    line-height: 1.42;
    display: flex;
    gap: 100px;
    margin: auto 0 10px;

    @include tablet {
      flex-direction: column;
      gap: 20px; }

    a {
      color: $white; } }

  &__desc {
    max-width: 576px;
    font-size: clamp(14px, vw(18px), 18px);
    color: $white;
    line-height: 1.68;
    margin-bottom: 20px; }

  &__address {
    margin-top: auto; }

  &__map-link {
    margin-right: 150px;
    @include laptop {
      margin-right: 0; } }

  &__nav {
    position: absolute;
    right: 210px;
    top: 45px;
    z-index: 5;
    display: flex;
    gap: 24px;

    .icon {
      width: 68px;
      height: 37px; }

    .js-slider-prev,
    .js-slider-next {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 80px;
      height: 80px;
      cursor: pointer;
      opacity: 1;
      transition: all ease 0.1s;

      &.swiper-button-disabled {
        opacity: 0.3; } }

    @include md {
      right: 20px;
      top: 40px;
      .js-slider-prev,
      .js-slider-next {
        height: 40px;
        width: 40px; }
      .icon {
        width: calc(68px / 2);
        height: calc(37px / 2); } } } }
