.features {
	position: relative;
	padding: 0;

	.container {
		max-width: 1640px;
		padding: 0; }

	.section__title {
		box-sizing: border-box;
		max-width: 1308px;
		padding-top: clamp(20px, vw(80px), 80px);
		padding-left: clamp(20px, 5vw, 5vw);
		padding-right: clamp(20px, 5vw, 5vw); }

	&__btn {
		.btn {
			max-width: 222px;
			margin: 0 auto; } }

	&.with-btns {
		padding-bottom: 112px;

		@include laptop {
			padding-bottom: 80px; }
		@include tablet {
			padding-bottom: 72px; }
		@include md {
			padding-bottom: 103px; }
		@include sm {
			padding-bottom: 40px; } } }

.features-block {
	display: flex;
	margin: 0;

	@include tablet {
		margin: 0; }
	@include md {
		margin: 0; }
	@include xs {
		flex-direction: column; }


	&__item {
		box-sizing: border-box;
		position: relative;
		display: flex;
		flex-direction: column;
		flex: 1 1;
		padding: 42px 45px;
		min-height: 712px;
		min-width: 0;
		@include laptop {
			padding: 3vw 3.2vw;
			min-height: 44vw; }
		@include xs {
			padding: 6vw 6vw;
			min-height: 70vw; }
		@include xxs {
			min-height: 95vw; } }

	&__title {
		position: relative;
		text-transform: uppercase;
		font-size: 58px;
		margin: 0 0 auto;
		line-height: 1;
		z-index: 1;
		@include laptop {
			font-size: 3.5vw; }

		@include xs {
			font-size: 8.6vw;
			line-height: 1.2; }

		&_small {
			font-size: 50px;

			@include laptop {
				font-size: 3.5vw; } } }


	&__desc {
		position: relative;
		font-size: 28px;
		margin: 0 0 38px;
		line-height: 1.16;
		z-index: 1;
		@include laptop {
			font-size: 2vw;
			margin: 1.3vw 0 2.6vw;
			line-height: 1.4; }
		@include xs {
			margin: 5vw 0 5vw;
			font-size: 16px; } }

	&__links {
		font-size: 20px;
		position: relative;
		z-index: 1;

		@include laptop {
			font-size: 1.8vw; }

		@include xs {
			font-size: 14px; } }

	&__link {
		color: #fff;
		margin: 2px 0;
		display: inline-block;
		text-decoration: underline;

		&:hover {
			text-decoration: none; } }


	&__back {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 0;
		overflow: hidden;

		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: rgba(0, 0, 0, 0.5); } }

	&__back-pic {
		width: 100%;
		height: 100%;
		display: block;
		object-fit: cover; } }


.networkers {
	display: flex;
	flex-wrap: wrap;

	&__item {
		position: relative;
		width: 33.3%;
		height: calc(712px / 4);
		@include laptop {
			height: calc(44vw / 4); }
		@include xs {
			width: 25%;
			height: calc(70vw / 3); }
		@include xxs {
			width: 33.3%;
			height: calc(95vw / 2); }

		img {} }


	&__speaker {
		width: 100%;
		height: 100%;
		display: block;
		object-fit: cover; }

	&__time {
		position: absolute;
		bottom: 8px;
		right: 8px; }

	&__face-wrap {
		display: flex;
		align-items: center;
		justify-content: center;
		width: calc(100% - 4px);
		height: calc(100% - 4px);
		margin: 2px;
		background: #000;
		border-radius: 5px; }

	&__face {
		position: relative;
		display: block;
		height: 70%;
		width: 100%;
		fill: $mainBg; } }


.features-btns {
	margin: 64px 0 0;

	@include tablet {
		margin: 50px 0 0; }
	@include md {
		margin: 30px 0 0; } }
