@mixin light {
  font-family: 'Montserrat', sans-serif;
  font-weight: 300; }

@mixin regular {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400; }

@mixin medium {
  font-family: 'Montserrat', sans-serif;
  font-weight: 500; }

@mixin bold {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700; }

@mixin black {
  font-family: 'Montserrat', sans-serif;
  font-weight: 900; }

.out {
  overflow: hidden;
  position: relative; }

//html
////removed becaus it delays navigation on page load
///scroll-behavior: smooth

body {
  background: $mainBg;
  font-size: 14px;
  line-height: 1.2;
  color: $white;
  @include regular;
  &.is-no-scroll {
    overflow: hidden; } }

a {
  color: $themeColor;
  &:hover {
    text-decoration: none; } }

.text {
  letter-spacing: 0.7px;
  font-size: 22px;
  line-height: 1.7;
  text-align: center;
  //+laptop
  font-size: 18px;
  @include tablet {
    font-size: 19px;
    letter-spacing: 1.1px;
    line-height: 1.8; }
  @include md {
    font-size: 16px;
    line-height: 1.8; }
  @include xs {
    font-size: 15px; }
  a {
    color: inherit; } }

.main {
  display: flex;
  flex-direction: column;
  &.blur {
    filter: blur(10px) grayscale(.6); } }


#popup-ticket {

  .pop-ticket {

    &__btn {
      color: $white;
      background: transparent;
      border: 2px solid $themeColor;

      &:hover {
        background: $themeColor; }

      &:after,
      &:before {
        display: none; } } } }
