.remote-switch {
  font-size: 12px;
  position: fixed;
  left: 50%;
  transform: translateX(-660px);
  top: 1;
  display: flex;
  height: 48px;
  align-items: center;
  padding: 0;
  background: transparent;
  z-index: 5;
  border-radius: 100px;
  pointer-events: auto;
  border: 2px solid $themeColor;
  display: none;


  &__text {
    opacity: 0.6;
    text-decoration: none;
    color: $mainBg;
    height: 100%;
    padding: 0 10px;
    display: flex;
    font-weight: 600;
    align-items: center;

    // +xs
    //font-size: 10px

    &:first-child {
      padding: 0 10px 0 20px;

      // +xs
 }      //padding: 0 10px 0 10px

    &:last-child {
      padding: 0 20px 0 10px;

      // +xs
 }      //padding: 0 10px 0 10px


    &:not(.active):hover {
      color: $themeColor;
      opacity: 1; }

    &.active {
      opacity: 1; } }

  &__switch {
    position: relative;
    width: 36px;
    height: 12px;
    border-radius: 6px;
    background: rgba(0, 0, 0, 0.2);
    cursor: pointer;

    @include xs {
      width: 22px;
      height: 10px; }

    &:after {
      content: '';
      position: absolute;
      left: -3px;
      top: -3px;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      background: $themeColor;

      @include xs {
        width: 14px;
        height: 14px;
        top: -2px;
        left: -2px; } }

    &.active {
      &:after {
        left: auto;
        right: -3px;

        @include xs {
          left: auto;
          right: -2px; } } } } }
